import React, { useRef, useState } from 'react';
import SignaturePad from 'react-signature-pad-wrapper';
import PdfReview from './PdfViewer';
import file from './assets/input.pdf'
import axios from 'axios';
const SignatureComponent = () => {
  const sigRef = useRef(null);
    const [ isLoading, setIsLoading ] = useState(false)
  const handleSave = () => {
    console.log("sigRef.current",sigRef.current)
    const dataURL = sigRef.current?.toDataURL();
    setIsLoading(true)
    axios.post('https://pdfsignnode.onrender.com/upload',{
        "filename": "fouad.png",
        "base64":dataURL
    }).then((res)=>{
        downloadPDF(res.data.data)
    }).finally(()=>{
        setIsLoading(false)
    })
  };
  const handleClear = () => {
    sigRef.current.signaturePad.clear();
  }

  function downloadPDF(pdf) {
    const linkSource = `data:application/pdf;base64,${pdf}`;
    const downloadLink = document.createElement("a");
    const fileName = "abc.pdf";
    downloadLink.href = linkSource;
    downloadLink.download = fileName;
    downloadLink.click();
}
if(isLoading) {
    return <div>loading......</div>
}
  return (
    <div>
        <PdfReview file={file} />
        <SignaturePad ref={sigRef} />
        <button onClick={handleSave}>Save</button>
        <button onClick={handleClear}>Clear</button>
    </div>
  );
};

export default SignatureComponent;
